/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Sora:wght@400;500;600;700&display=swap');

/* CSS Custom Properties (Design Tokens) */
:root {
  /* Colors */
  --color-primary: #1285ff;
  --color-primary-light: #4da3ff;
  --color-primary-dark: #0a6dd6;
  --color-text-dark: #181819;
  --color-text-medium: #5a5a5b;
  --color-text-muted: #888889;
  --color-border: #e8e9ea;
  --color-background: #ffffff;
  
  /* Typography - Only 4 sizes */
  --font-primary: 'Sora', -apple-system, BlinkMacSystemFont, sans-serif;
  --font-size-xl: 44px;   /* Headline */
  --font-size-lg: 18px;   /* Subheadline, buttons */
  --font-size-md: 15px;   /* Body, links */
  --font-size-sm: 13px;   /* Captions, footer */
  
  /* Shadows */
  --shadow-lg: 0 8px 30px rgba(0, 0, 0, 0.08);
  --shadow-primary: 0 4px 14px rgba(18, 133, 255, 0.35);
  --shadow-primary-hover: 0 6px 20px rgba(18, 133, 255, 0.45);
  
  /* Border Radius */
  --border-radius-md: 10px;
  --border-radius-lg: 16px;
  
  /* Transitions */
  --transition-fast: 150ms ease;
  --transition-base: 200ms ease;
}

/* Reset & Base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Page Layout */
.signup-page {
  min-height: 100vh;
  background: linear-gradient(165deg, #ffffff 0%, #f8fafc 50%, #f0f4f8 100%);
  display: flex;
  flex-direction: column;
  font-family: var(--font-primary);
  position: relative;
  overflow: hidden;
}

/* Decorative Background Orbs */
.bg-gradient-orb {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.5;
  pointer-events: none;
  animation: float 20s ease-in-out infinite;
}

.bg-orb-1 {
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(18, 133, 255, 0.12) 0%, transparent 70%);
  top: -200px;
  right: -150px;
}

.bg-orb-2 {
  width: 500px;
  height: 500px;
  background: radial-gradient(circle, rgba(18, 133, 255, 0.10) 0%, transparent 70%);
  bottom: -100px;
  left: -100px;
  animation-delay: -10s;
}

@keyframes float {
  0%, 100% { transform: translate(0, 0) scale(1); }
  33% { transform: translate(30px, -30px) scale(1.05); }
  66% { transform: translate(-20px, 20px) scale(0.95); }
}

/* Header & Logo */
.signup-header {
  padding: 32px 48px;
  position: relative;
  z-index: 10;
  animation: fadeInDown 0.6s ease-out;
  text-align: center;
}

.logo-text {
  font-family: var(--font-primary);
  font-size: 32px;
  font-weight: 400;
  color: var(--color-primary);
  letter-spacing: -0.5px;
  cursor: pointer;
  transition: opacity var(--transition-base);
}

.logo-text:hover {
  opacity: 0.8;
}

/* Main Content */
.signup-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 24px 60px;
  position: relative;
  z-index: 10;
}

.signup-content {
  width: 100%;
  max-width: 480px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Wider content for form steps */
.step-2 .signup-card,
.step-3 .signup-card,
.step-5 .signup-card {
  max-width: 420px;
}

.signup-card-form {
  max-width: 420px;
}

/* Headlines */
.headline-section {
  text-align: center;
  margin-bottom: 40px;
  animation: fadeInUp 0.6s ease-out;
}

.signup-headline {
  font-size: var(--font-size-xl);
  font-weight: 700;
  color: var(--color-text-dark);
  line-height: 1.15;
  margin-bottom: 16px;
  letter-spacing: -1px;
  white-space: nowrap;
}

.signup-headline-wrap {
  white-space: normal;
  max-width: 900px;
  line-height: 1.2;
}

.signup-subheadline {
  font-size: var(--font-size-lg);
  font-weight: 500;
  color: var(--color-text-medium);
  line-height: 1.4;
}

/* Step Container */
.step-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* Signup Card */
.signup-card {
  width: 100%;
  background: var(--color-background);
  border-radius: var(--border-radius-lg);
  padding: 32px;
  box-shadow: var(--shadow-lg);
  border: 1px solid rgba(0, 0, 0, 0.04);
  animation: fadeInUp 0.4s ease-out;
}

/* Signup Buttons Container */
.signup-buttons {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* Primary Button */
.btn-primary {
  width: 100%;
  height: 54px;
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primary-dark) 100%);
  color: var(--color-background);
  border: none;
  border-radius: var(--border-radius-md);
  font-family: var(--font-primary);
  font-size: var(--font-size-lg);
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  box-shadow: var(--shadow-primary);
  transition: all var(--transition-base);
  position: relative;
  overflow: hidden;
}

.btn-primary::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, var(--color-primary-light) 0%, var(--color-primary) 100%);
  opacity: 0;
  transition: opacity var(--transition-base);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-primary-hover);
}

.btn-primary:hover::before {
  opacity: 1;
}

.btn-primary:active {
  transform: translateY(0);
}

.btn-primary span,
.btn-primary svg {
  position: relative;
  z-index: 1;
}

.btn-primary svg {
  transition: transform var(--transition-base);
}

.btn-primary:hover svg {
  transform: translateX(4px);
}

.btn-primary:focus-visible {
  outline: 3px solid rgba(18, 133, 255, 0.4);
  outline-offset: 2px;
}

/* Divider */
.divider {
  display: flex;
  align-items: center;
  gap: 16px;
  margin: 8px 0;
}

.divider-line {
  flex: 1;
  height: 1px;
  background: var(--color-border);
}

.divider-text {
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--color-text-muted);
  text-transform: lowercase;
}

/* Social Login Buttons */
.social-buttons {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.btn-social {
  width: 100%;
  height: 52px;
  background: var(--color-background);
  border: 1.5px solid var(--color-border);
  border-radius: var(--border-radius-md);
  font-family: var(--font-primary);
  font-size: var(--font-size-md);
  font-weight: 500;
  color: var(--color-text-dark);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  transition: all var(--transition-base);
  padding: 0 20px;
}

/* Icon wrapper for consistent alignment */
.btn-social-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
}

.btn-social-icon svg {
  display: block;
}

.btn-social-text {
  line-height: 1;
}

.btn-social:hover {
  border-color: var(--color-primary-light);
  background: rgba(18, 133, 255, 0.02);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(18, 133, 255, 0.06);
}

.btn-social:active {
  transform: translateY(0);
  background: rgba(18, 133, 255, 0.04);
}

.btn-social:focus-visible {
  outline: 3px solid rgba(18, 133, 255, 0.3);
  outline-offset: 2px;
}

/* Login Link */
.login-text {
  font-size: var(--font-size-md);
  font-weight: 500;
  color: var(--color-text-medium);
  text-align: center;
  margin-top: 8px;
}

.login-link {
  color: var(--color-primary);
  text-decoration: none;
  font-weight: 600;
  transition: color var(--transition-fast);
}

.login-link:hover {
  color: var(--color-primary-dark);
  text-decoration: underline;
}

/* Form Styles */
.signup-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.form-field {
  width: 100%;
}

.form-input {
  width: 100%;
  height: 52px;
  padding: 0 16px;
  background: var(--color-background);
  border: 1.5px solid var(--color-border);
  border-radius: var(--border-radius-md);
  font-family: var(--font-primary);
  font-size: var(--font-size-md);
  font-weight: 500;
  color: var(--color-text-dark);
  transition: all var(--transition-base);
}

.form-input::placeholder {
  color: var(--color-text-muted);
  font-weight: 500;
}

.form-input:hover {
  border-color: var(--color-primary-light);
}

.form-input:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(18, 133, 255, 0.1);
}

/* Disabled Button */
.btn-disabled {
  opacity: 0.6;
  cursor: not-allowed;
  box-shadow: none;
}

.btn-disabled:hover {
  transform: none;
  box-shadow: none;
}

.btn-disabled::before {
  display: none;
}

/* Confirmation Screen (Step 4) */
.confirmation-content {
  text-align: center;
  animation: fadeInUp 0.4s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}

.confirmation-success {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: var(--font-size-md);
  font-weight: 600;
  color: #10b981;
  background: rgba(16, 185, 129, 0.08);
  padding: 10px 20px;
  border-radius: 100px;
}

.confirmation-help-card {
  background: var(--color-background);
  border-radius: var(--border-radius-lg);
  padding: 28px 32px;
  box-shadow: var(--shadow-lg);
  border: 1px solid rgba(0, 0, 0, 0.04);
  max-width: 380px;
  width: 100%;
}

.confirmation-help-title {
  font-size: var(--font-size-lg);
  font-weight: 600;
  color: var(--color-text-dark);
  margin-bottom: 8px;
}

.confirmation-help-text {
  font-size: var(--font-size-md);
  color: var(--color-text-medium);
  margin-bottom: 20px;
}

.confirmation-actions {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.confirmation-btn {
  width: 100%;
  height: 48px;
  background: var(--color-primary);
  color: white;
  border: none;
  border-radius: var(--border-radius-md);
  font-family: var(--font-primary);
  font-size: var(--font-size-md);
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-base);
}

.confirmation-btn:hover {
  background: var(--color-primary-dark);
}

.confirmation-btn-secondary {
  width: 100%;
  height: 48px;
  background: transparent;
  color: var(--color-primary);
  border: 1.5px solid var(--color-border);
  border-radius: var(--border-radius-md);
  font-family: var(--font-primary);
  font-size: var(--font-size-md);
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-base);
}

.confirmation-btn-secondary:hover {
  border-color: var(--color-primary-light);
  background: rgba(18, 133, 255, 0.02);
}

.confirmation-demo-btn {
  margin-top: 16px;
  background: none;
  border: none;
  font-family: var(--font-primary);
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
  cursor: pointer;
  padding: 8px;
  transition: color var(--transition-fast);
}

.confirmation-demo-btn:hover {
  color: var(--color-primary);
}

/* Terms of Service */
.tos-text {
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--color-text-muted);
  text-align: center;
  margin-top: 24px;
  animation: fadeInUp 0.6s ease-out 0.2s both;
}

.tos-link {
  color: var(--color-primary);
  text-decoration: none;
  transition: color var(--transition-fast);
}

.tos-link:hover {
  color: var(--color-primary-dark);
  text-decoration: underline;
}

/* Footer */
.signup-footer {
  padding: 24px 24px 32px;
  text-align: center;
  position: relative;
  z-index: 10;
  animation: fadeInUp 0.6s ease-out 0.3s both;
}

.footer-badges {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-bottom: 16px;
}

.footer-badge {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--color-text-muted);
}

.footer-badge svg {
  opacity: 0.7;
}

.footer-links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 8px 24px;
}

.footer-links a {
  font-size: var(--font-size-sm);
  font-weight: 500;
  color: var(--color-text-muted);
  text-decoration: none;
  transition: color var(--transition-fast);
}

.footer-links a:hover {
  color: var(--color-text-medium);
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 540px) {
  .signup-header {
    padding: 24px;
  }
  
  .signup-headline {
    font-size: 32px;
    white-space: normal;
  }
  
  .signup-card {
    padding: 24px;
    border-radius: var(--border-radius-md);
  }
  
  .btn-social {
    height: 48px;
  }
  
  .footer-badges {
    flex-direction: column;
    gap: 8px;
  }
  
  .bg-orb-1,
  .bg-orb-2 {
    opacity: 0.3;
  }
}

/* Prefers reduced motion */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .bg-gradient-orb {
    animation: none;
  }
}
